  .header-contacts {
    width: 100px;
    margin-right: 50px;
    justify-content: space-between;
  }
}

.header-contacts__item {
  padding: 10px;
}

.header-contacts__item + .header-contacts__item {
  margin-left: 100px;
}

.header-contacts__link {
  max-width: 225px;
