}

.header-contacts__icon {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .header-contacts__icon {
    margin: 0;
  }
}

.header-contacts__description {
  color: #f2f5f7;
  font-family: "PT Mono", "Arial", sans-serif;
